body {
  font-family: 'Cairo', sans-serif;
}
.contaner {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  width: 100%;
  border-radius: var(--border-radius);
  background-color: var(--backgound-2);
  color: var(--primary-dark);
  height: 100%;
  overflow-y: scroll;
}
.form {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  width: 100%;
  margin: auto;
  gap: var(--gap);
  padding: var(--pading) 0;
}
.field {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: flex-end;
  flex-wrap: wrap;
  width: 90%;
  border: solid 1px var(--border-color);
  padding: var(--pading);
  border-radius: var(--border-radius);
  margin: auto;
  gap: var(--gap);
}
.field h4 {
  border-bottom: solid 1px var(--border-color);
}
.inputs {
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  justify-content: center;
  flex-wrap: wrap;
  width: 100%;
  gap: var(--gap);
}
.input {
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  justify-content: center;
  flex-wrap: wrap;
  gap: var(--gap);
}

.btn{
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  gap: var(--gap);
  width: 80%;
  margin: auto;
  padding: var(--pading);
}

.btn button:disabled{
  background-color: var(--primary-dark);
  color: var(--primary-light);
  border: solid 1px var(--primary-dark);
  scale: 1;
  cursor: not-allowed;
}

.loader {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1;
}