.loader {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9999;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 0.4s ease-in-out;
  opacity: 1;
  visibility: visible;
  font-size: var(--font-size-small);
  color: var(--primary-base);
  font-weight: 800;
  gap: var(--gap);
}
